@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@200;300;400;600;700;800;900&display=swap');

// Typography
@font-family: 'Raleway', sans-serif;
@typography-title-margin-top: 0px;
@typography-title-margin-bottom: 0px;

h1.@{ant-prefix}-typography {
  font-weight: 700 !important;
  @media (max-width: 767.9px) {
    font-size: 30px !important;
  }
}

h2.@{ant-prefix}-typography {
  font-weight: 700 !important;
  @media (max-width: 767.9px) {
    font-size: 24px !important;
  }
}

h3.@{ant-prefix}-typography {
  font-weight: 700 !important;
  @media (max-width: 767.9px) {
    font-size: 20px !important;
  }
}

h4.@{ant-prefix}-typography {
  font-weight: 700 !important;
  @media (max-width: 767.9px) {
    font-size: 16px !important;
  }
}

h5.@{ant-prefix}-typography {
  font-weight: 700 !important;
  @media (max-width: 767.9px) {
    font-size: 14px !important;
  }
}

span.@{ant-prefix}-typography {
  &.caption {
    font-size: 12px !important;
  }
}
